html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
  overflow: auto;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  font-family: "Hahmlet", serif;
  --color-primary: #b8e5e0;
  --color-secondary: #ccd2f0;
  --color-highlight: #ffe17d;
}
.material-symbols-rounded {
  font-variation-settings: "FILL" 0, "wght" 100, "GRAD" -25, "opsz" 24;
}

::-webkit-scrollbar {
  background-color: transparent;
  width: 10px;
}
::-webkit-scrollbar-thumb {
  background: #ddd;
  border: 1px solid #ccc;
}

::-webkit-scrollbar-track {
  background-color: white;
}

input {
  display: inline-block;
  width: 95%;
  padding: 0.5rem;
  border-radius: 10px;
  border: 1px solid #aaa;
  transition-duration: 0.3s;
  outline-color: var(--color-secondary);
}

textarea {
  display: inline-block;
  width: 95%;
  padding: 0.5rem;

  border-radius: 10px;
  border: 1px solid #aaa;
  transition-duration: 0.3s;
  outline-color: var(--color-secondary);
}
